.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  padding: 32px 16px 16px;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;

  &__img {
    width: 160px;
    height: 134px;
    margin: 0 auto 40px ;
  }

  &__title {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 4px;
  }

  &__code {
    color: $secondaryColor;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    margin-bottom: 16px;
  }

  &__reviews-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }

  &__review {
    font-size: 10px;
    line-height: 14px;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
  }

  &__price-amount {
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
  }

  &__price-text {
    font-size: 12px;
    line-height: 18px;
    color: $secondaryColor;
  }

  &__button {
    display: flex;
    width: 166px;
    height: 40px;
    justify-content: center;
    align-items: center;

    text-decoration: none;
    text-transform: uppercase;
    line-height: 16px;
    font-weight: 700;
    font-size: 14px;
    border: 1px solid $buttonColor ;
    border-radius: 5px;
    background-color: $buttonColor;
    color: #fff;
    &:hover {
      color: $buttonColor;
      background-color: #fff;
    }
  }
}
